@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
body {
  background-color: #d3d3d3;
  font-family: Montserrat, sans-serif;
}

a {
  color: #2e2f42;
  margin-bottom: 50px;
  font-size: 20px;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

a:hover {
  color: #f08080;
}

.form {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 25%;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

label, input, button {
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

.form > label {
  margin-bottom: 8px;
}

.form > label > input {
  border: 2px solid silver;
  border-radius: 4px;
  width: calc(100% - 12px);
  margin-top: 4px;
  padding-block: 4px;
  padding-inline: 4px;
  display: block;
}

fieldset {
  text-align: center;
  border-style: solid;
  border-radius: 4px;
  margin-inline: 0;
}

fieldset > legend {
  letter-spacing: .04em;
  text-align: left;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4e75ff;
  border: none;
  border-radius: 8px;
  min-width: 95px;
  height: 40px;
  margin-top: 8px;
  padding-block: 8px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover, button:focus {
  background-color: #6c8cff;
}
